<template>
  <canvas id="canvas"></canvas>  
</template>

<script>
export default {
  data () {
    return {
      canvas: '',
      ctx: '',
      startRadian: 0, // åå§è§åº¦
      canBeClick: true, // æ§å¶æ½å¥è¿è¡ä¸­ä¸è®©åæ½å¥
    }
  },
  props: {
    value: { type: Number, default: 0 },                   // ä¸­å¥çç´¢å¼
    awards: { type: Array, default: [] },                  // å¥åæ°ç»
    rate: { type: Number, default: 80 },                   // è½¬çéç
    radius: { type: Number, default: 180 },                // è½¬çåå¾
    textFontSize: { type: String, default: '13px' },       // æå­å¤§å°
    lineHeight: { type: Number, default: 20 },             // æå­è¡é«
    textColor: { type: String, default: '#d64737' },       // æå­é¢è²
    textMargin: { type: Number, default: 30 },             // æå­è·ç¦»è¾¹æ¡è·ç¦»
    textPadding: { type: Number, default: 0 },             // æå­è¡¥å¿å®½åº¦
    btnFontSize: { type: String, default: '26px' },        // æé®æå­å¤§å°
    btnColor: { type: String, default: '#d64737' },        // æé®æä»¶é¢è²
    btnBorderColor1: { type: String, default: '#d64737' }, // æé®å¤è¾¹æ¡é¢è²
    btnBorderColor2: { type: String, default: '#fff',   }, // æé®åè¾¹æ¡é¢è²
    btnBorderColor3: { type: String, default: '#f6c66f' }, // æé®æéé¢è²
    btnBgColor: { type: String, default: '#ffdea0' },      // æé®èæ¯é¢è²
    btnText: { type: String, default: 'æ½å¥' },            // æé®åå®¹
    btnRadius: { type: Number, default: 60 },              // æé®åå¾
    borderColor: { type: String, default: '#d64737' },     // è¾¹æ¡é¢è²
  },
  mounted () {
    this.initCanvas()
  },
  methods: {
    /**
     * åå§åè½¬ç
     */
    initCanvas() {
      this.canvas = document.querySelector('#canvas')
      this.ctx = canvas.getContext('2d')
      this.canvas.width = this.radius * 2
      this.canvas.height = this.radius * 2
      this.render()
      this.startRotate()
    },
    /**
     * å¤çæå­æ¢è¡
     */
    getLineTextList(ctx, text, maxLineWidth) {
      maxLineWidth += this.textPadding
      let wordList = text.split(''), tempLine = '', lineList = []
      for (let i = 0; i < wordList.length; i++) {
        if (ctx.measureText(tempLine).width >= maxLineWidth) {
          lineList.push(tempLine)
          maxLineWidth -= ctx.measureText(text[0]).width
          tempLine = ''
        }
        tempLine += wordList[i]
      }
      lineList.push(tempLine)
      return lineList
    },

    /**
     * ç»å¶è½¬ç
     */
    drawPanel() {
      const ctx = this.ctx
      const startRadian = this.startRadian
      ctx.save()
      ctx.beginPath()
      ctx.fillStyle = this.borderColor
      // æ ¹æ®æä»¬è®¾å®çåå§è§åº¦æ¥ç»å¶è½¬ç
      ctx.arc(this.radius, this.radius, this.radius, startRadian, Math.PI * 2 + startRadian, false)
      ctx.fill()
      ctx.restore()
    },

    /**
     * ç»å¶å¥å
     */
    drawPrizeBlock() {
      const ctx = this.ctx
      const awards = this.awards
      // æ ¹æ®åå§è§åº¦æ¥ç»å¶å¥åå
      let startRadian = this.startRadian
      let RadianGap = Math.PI * 2 / awards.length
      let endRadian = startRadian + RadianGap
      for (let i = 0; i < awards.length; i++) {
        ctx.save()
        ctx.beginPath()
        ctx.fillStyle = awards[i].color || i % 2 == 0 ? '#f8d384' : '#f9e3bb'
        ctx.moveTo(this.radius, this.radius)
        ctx.arc(this.radius, this.radius, this.radius - 20, startRadian, endRadian, false)
        ctx.fill()
        ctx.restore()

        ctx.save()
        ctx.fillStyle = this.textColor
        ctx.font = `${this.textFontSize} Arial`
        ctx.translate(
          this.radius + Math.cos(startRadian + RadianGap / 2) * (this.radius - this.textMargin),
          this.radius + Math.sin(startRadian + RadianGap / 2) * (this.radius - this.textMargin)
        )
        ctx.rotate(startRadian + RadianGap / 2 + Math.PI / 2)
        this.getLineTextList(ctx, awards[i].name, 70).forEach((line, index) => {
          ctx.fillText(line, -ctx.measureText(line).width / 2, ++index * this.lineHeight)
        })
        ctx.restore()

        startRadian += RadianGap
        endRadian += RadianGap
      }
    },

    // å°canvasåwindowä¸­çåæ ç¹è½¬åä¸ºcanvasä¸­çåæ ç¹
    windowToCanvas(canvas, e) {
      // getBoundingClientRectè¿ä¸ªæ¹æ³è¿åhtmlåç´ çå¤§å°åå¶ç¸å¯¹äºè§å£çä½ç½®
      const canvasPostion = canvas.getBoundingClientRect(), x = e.clientX, y = e.clientY
      return {
        x: x - canvasPostion.left,
        y: y - canvasPostion.top
      }
    },

    // åå§å
    startRotate() {
      const canvas = this.canvas
      const ctx = this.ctx
      const canvasStyle = canvas.getAttribute('style');
      this.render()
      canvas.addEventListener('mousedown', e => {
        // åªè¦æ½å¥æ²¡æç»æï¼å°±ä¸è®©åæ¬¡æ½å¥
        if (this.value < 0 || this.value >= this.awards.length) console.error('è¯¥ç´¢å¼çå¥åä¸å­å¨!')
        if (!this.canBeClick || this.value < 0 || this.value >= this.awards.length) return false
        this.canBeClick = false
        let loc = this.windowToCanvas(canvas, e)
        ctx.beginPath()
        ctx.arc(this.radius, this.radius, 50, 0, Math.PI * 2, false)
        if (ctx.isPointInPath(loc.x, loc.y)) {
          this.$emit('start')
          // æ¯æ¬¡ç¹å»æ½å¥ï¼é½å°åå§åè§åº¦éç½®
          this.startRadian = -Math.floor(Math.random() * 180)
          // distanceæ¯è®¡ç®åºçå°æå®å¥åæè½¬å°æéå¤éè¦æè½¬çè§åº¦è·ç¦»ï¼distanceToStopä¸é¢ä¼åè¯´æ
          const distance = this.distanceToStop()
          this.rotatePanel(distance)
        } else {
          this.canBeClick = true
        }
      })
      canvas.addEventListener('mousemove', e => {
        let loc = this.windowToCanvas(canvas, e)
        ctx.beginPath()
        ctx.arc(this.radius, this.radius, 30, 0, Math.PI * 2, false)
        if (ctx.isPointInPath(loc.x, loc.y)) {
          canvas.setAttribute('style', `cursor: pointer;${canvasStyle}`)
        } else {
          canvas.setAttribute('style', canvasStyle)
        }
      })
    },

    // å¤çæè½¬çå³é®æ¹æ³
    rotatePanel(distance) {
      // è¿éç¨ä¸ä¸ªå¾ç®åçç¼å¨å½æ°æ¥è®¡ç®æ¯æ¬¡ç»å¶éè¦æ¹åçè§åº¦ï¼è¿æ ·å¯ä»¥è¾¾å°ä¸ä¸ªè½¬çä»åå°æ¢çæ¸åçè¿ç¨
      let changeRadian = (distance - this.startRadian) / this.rate
      this.startRadian += changeRadian
      // å½æåçç®æ è·ç¦»ä¸startRadianä¹é´çå·®è·ä½äº0.05æ¶ï¼å°±é»è®¤å¥åæ½å®äºï¼å¯ä»¥ç»§ç»­æ½ä¸ä¸ä¸ªäºã
      if (distance - this.startRadian <= 0.05) {
        this.$emit('end', this.value)
        return this.canBeClick = true
      }
      this.render()
      window.requestAnimationFrame(this.rotatePanel.bind(this, distance))
    },

    // ç»å¶æé®ï¼ä»¥åæé®ä¸startçæå­
    drawButton() {
      const ctx = this.ctx
      ctx.save()
      ctx.beginPath()
      ctx.fillStyle = this.btnBorderColor1
      ctx.arc(this.radius, this.radius, this.btnRadius, 0, Math.PI * 2, false)
      ctx.fill()
      ctx.restore()

      ctx.save()
      ctx.beginPath()
      ctx.fillStyle = this.btnBorderColor2
      ctx.arc(this.radius, this.radius, this.btnRadius - 5, 0, Math.PI * 2, false)
      ctx.fill()
      ctx.restore()

      ctx.save()
      ctx.beginPath()
      ctx.fillStyle = this.btnBorderColor3
      ctx.arc(this.radius, this.radius, this.btnRadius - 10, 0, Math.PI * 2, false)
      ctx.fill()
      ctx.restore()

      ctx.save()
      ctx.beginPath()
      ctx.fillStyle = this.btnBorderColor3
      ctx.moveTo(this.radius - this.btnRadius/2, this.radius - this.btnRadius/2 - 5)
      ctx.lineTo(this.radius, this.radius - this.btnRadius - 25)
      ctx.lineTo(this.radius + this.btnRadius/2, this.radius - this.btnRadius/2 - 5)
      ctx.closePath()
      ctx.fill()
      ctx.restore()

      ctx.save()
      ctx.beginPath()
      ctx.fillStyle = this.btnBgColor
      ctx.arc(this.radius, this.radius, this.btnRadius - 20, 0, Math.PI * 2, false)
      ctx.fill()
      ctx.restore()
      
      ctx.save()
      ctx.beginPath()
      ctx.fillStyle = this.btnColor
      ctx.font = `${this.btnFontSize} Arial`
      ctx.translate(this.radius, this.radius)
      ctx.fillText(this.btnText, -ctx.measureText(this.btnText).width / 2, 8)
      ctx.restore()
    },

    distanceToStop() {
      // middleDegreesä¸ºå¥ååçä¸­é´è§åº¦ï¼æç»åçé½æ¯ä»¥ä¸­é´è§åº¦è¿è¡è®¡ç®çï¼è·ç¦»åå§çstartRadiançè·ç¦»ï¼distanceå°±æ¯å½åå¥åè·å°æéä½ç½®è¦è½¬å¨çè·ç¦»ã
      let middleDegrees = 0, distance = 0
      // æ å°åºæ¯ä¸ªå¥åçmiddleDegrees
      const awardsToDegreesList = this.awards.map((data, index) => {
        let awardRadian = (Math.PI * 2) / this.awards.length
        return awardRadian * index + (awardRadian * (index + 1) - awardRadian * index) / 2
      });
      // æ­¤æ¬¡æ½å¥åºè¯¥ä¸­çå¥å
      const currentPrizeIndex = this.value
      middleDegrees = awardsToDegreesList[currentPrizeIndex];
      // å ä¸ºæéæ¯åç´åä¸çï¼ç¸å½åæ ç³»çMath.PI/2,æä»¥è¿éè¦è¿è¡å¤æ­æ¥ç§»å¨è§åº¦
      distance = Math.PI * 3 / 2 - middleDegrees
      distance = distance > 0 ? distance : Math.PI * 2 + distance
      // è¿éé¢å¤å ä¸åé¢çå¼ï¼æ¯ä¸ºäºè®©è½¬çå¤è½¬å¨å åï¼çä¸å»æ´åæ¯å¨æ½å¥
      return distance + Math.PI * 10;
    },

    render() {
      this.drawPanel()
      this.drawPrizeBlock()
      this.drawButton()
    }
  }
}
</script>
